"use client"

import { useState, useEffect } from "react"
import { useParams } from "next/navigation"
import Link from "next/link" // <-- PERBAIKAN: Import Link sudah ditambahkan
import Header from "@/components/header"
import Footer from "@/components/footer"
import Gallery from "@/components/gallery"
import RatingStars from "@/components/rating-stars"
import BookmarkButton from "@/components/bookmark-button"
import ReviewsSection from "@/components/reviews-section"
import { AccordionItem } from "@/components/accordion"
import { MapPin, Navigation, ShoppingBag, Copy, Info } from "lucide-react"
import { calculateDistance } from "@/lib/utils"
import { loadJSON } from "@/lib/data"

export default function DetailKulinerPage() {
  const params = useParams()
  const [item, setItem] = useState<any>(null)
  const [userLocation, setUserLocation] = useState<{ lat: number; lng: number } | null>(null)
  const [distance, setDistance] = useState<number | null>(null)
  const [loading, setLoading] = useState(true)
  const [copied, setCopied] = useState(false)

  // 1. Fetch Data berdasarkan ID dari URL
  useEffect(() => {
    async function fetchData() {
      try {
        const allData = await loadJSON<any[]>("/mock/home/kuliner.json")
        const foundItem = allData.find((d) => d.id === params.id)
        
        // Mock koordinat UMKM (Nanti ambil dari Database Laravel)
        if (foundItem) {
          const enhancedItem = {
            ...foundItem,
            lat: foundItem.lat || -6.2000, 
            lng: foundItem.lng || 106.8166,
            deskripsi: {
              ringkas: `Kuliner khas ${foundItem.provinsi} yang sangat populer.`,
              lengkap: `Nikmati cita rasa autentik ${foundItem.title} yang dibuat dengan resep turun temurun menggunakan rempah pilihan asli Nusantara.`
            },
            resep: {
              bahan: ["Bahan Utama", "Rempah Pilihan", "Santan Segar"],
              langkah: ["Persiapkan bahan", "Tumis bumbu", "Masak hingga matang"]
            }
          }
          setItem(enhancedItem)
        }
      } catch (err) {
        console.error("Error loading detail:", err)
      } finally {
        setLoading(false)
      }
    }
    fetchData()
  }, [params.id])

  // 2. Real-time Geolocation User
  useEffect(() => {
    if ("geolocation" in navigator) {
      navigator.geolocation.getCurrentPosition((position) => {
        setUserLocation({
          lat: position.coords.latitude,
          lng: position.coords.longitude
        })
      }, (err) => {
        console.warn("User menolak akses lokasi:", err)
      })
    }
  }, [])

  // 3. Hitung Jarak saat lokasi user & data item tersedia
  useEffect(() => {
    if (userLocation && item) {
      const d = calculateDistance(
        userLocation.lat,
        userLocation.lng,
        item.lat,
        item.lng
      )
      setDistance(d)
    }
  }, [userLocation, item])

  const copyLink = () => {
    if (typeof window !== "undefined") {
      navigator.clipboard.writeText(window.location.href)
      setCopied(true)
      setTimeout(() => setCopied(false), 3000)
    }
  }

  if (loading) return <div className="h-screen flex items-center justify-center bg-[#f4e8d1] font-serif text-[#a64029]">Sedang Menyiapkan Hidangan...</div>
  if (!item) return <div className="h-screen flex items-center justify-center bg-[#f4e8d1]">Kuliner tidak ditemukan.</div>

  return (
    <>
      <Header />
      <main className="bg-[#f4e8d1] min-h-screen pb-12">
        {/* Breadcrumb Terintegrasi */}
        <nav className="container mx-auto px-5 py-6 text-sm text-[#6e5849]">
          <Link href="/" className="hover:text-[#a64029]">Home</Link> / <span>{item.title}</span>
        </nav>

        <article className="container mx-auto px-5 max-w-7xl">
          <div className="grid grid-cols-1 lg:grid-cols-2 gap-12 items-start">
            
            {/* SISI KIRI: Visual & Location Info */}
            <div className="space-y-6">
              <Gallery images={item.images} />
              
              {/* Card Jarak Real-time */}
              <div className="bg-white p-6 rounded-2xl shadow-sm border border-[#3b2f2f]/10">
                <div className="flex items-center justify-between mb-4">
                  <div className="flex items-center gap-2 text-[#a64029] font-bold">
                    <MapPin className="w-5 h-5" />
                    <span>Lokasi UMKM</span>
                  </div>
                  {distance !== null ? (
                    <span className="bg-[#4e5b31]/10 text-[#4e5b31] px-3 py-1 rounded-full text-xs font-bold">
                      {distance} km dari lokasi Anda
                    </span>
                  ) : (
                    <span className="text-xs text-[#6e5849] animate-pulse italic">Mencari lokasi Anda...</span>
                  )}
                </div>
                <p className="text-[#3b2f2f] font-semibold">{item.kota}, {item.provinsi}</p>
                
                {/* Link Google Maps Dinamis berdasarkan Koordinat */}
                <a 
                  href={`https://www.google.com/maps?q=${item.lat},${item.lng}`}
                  target="_blank"
                  rel="noopener noreferrer"
                  className="mt-4 w-full flex items-center justify-center gap-2 py-3 bg-[#4e5b31] text-white rounded-xl hover:bg-[#3a4323] transition-all font-bold no-underline"
                >
                  <Navigation className="w-4 h-4" />
                  Buka di Google Maps
                </a>
              </div>
            </div>

            {/* SISI KANAN: Content & Action */}
            <div className="space-y-8">
              <header>
                <div className="flex gap-2 mb-4">
                  <span className="bg-[#dfaf2b] text-[#3b2f2f] px-3 py-1 rounded-lg text-xs font-bold uppercase tracking-wider">{item.kategori}</span>
                  <span className="bg-[#a64029] text-white px-3 py-1 rounded-lg text-xs font-bold italic">Top Rated</span>
                </div>
                <h1 className="font-serif text-5xl text-[#a64029] leading-tight mb-4">{item.title}</h1>
                <div className="flex items-center gap-6">
                  <RatingStars rating={Math.round(item.rating)} />
                  <span className="text-2xl font-sans font-bold text-[#3b2f2f]">
                    Rp {item.hargaMin?.toLocaleString('id-ID')}
                  </span>
                </div>
              </header>

              <section className="flex gap-3">
                <BookmarkButton kulinerId={item.id} />
                <button 
                  onClick={copyLink}
                  className="flex items-center gap-2 px-6 py-3 bg-white border border-[#ddd] rounded-xl font-bold text-[#6e5849] hover:bg-[#eee] transition-all"
                >
                  <Copy className="w-5 h-5" />
                  {copied ? "Berhasil Disalin!" : "Bagikan"}
                </button>
              </section>

              {/* Deskripsi Versatile */}
              <section className="bg-white/50 p-6 rounded-2xl border border-white">
                <h2 className="font-serif text-xl text-[#3b2f2f] mb-3 flex items-center gap-2">
                  <Info className="w-5 h-5 text-[#a64029]" />
                  Tentang Kuliner
                </h2>
                <p className="text-[#6e5849] leading-relaxed italic">{item.deskripsi.ringkas}</p>
                <p className="text-[#3b2f2f] leading-relaxed mt-4">{item.deskripsi.lengkap}</p>
              </section>

              {/* Perantara Pemesanan (Gojek, ShopeeFood dkk) */}
              <section>
                <h2 className="font-sans font-bold text-[#3b2f2f] mb-4">Pesan Sekarang Melalui:</h2>
                <div className="grid grid-cols-2 sm:grid-cols-3 gap-4">
                  {/* Link pencarian otomatis berdasarkan Nama Makanan */}
                  <a 
                    href={`https://gofood.link/p/search?query=${encodeURIComponent(item.title)}`}
                    target="_blank"
                    className="flex items-center justify-center gap-2 p-3 bg-white border border-[#ddd] rounded-xl hover:border-[#00aa13] hover:text-[#00aa13] transition-all font-bold no-underline text-[#3b2f2f]"
                  >
                    <ShoppingBag className="w-4 h-4" /> GoFood
                  </a>
                  <a 
                    href={`https://grab.com/food/search?query=${encodeURIComponent(item.title)}`}
                    target="_blank"
                    className="flex items-center justify-center gap-2 p-3 bg-white border border-[#ddd] rounded-xl hover:border-[#00b14f] hover:text-[#00b14f] transition-all font-bold no-underline text-[#3b2f2f]"
                  >
                    <ShoppingBag className="w-4 h-4" /> GrabFood
                  </a>
                  <a 
                    href={`https://shopee.co.id/search?keyword=${encodeURIComponent(item.title)}`}
                    target="_blank"
                    className="flex items-center justify-center gap-2 p-3 bg-white border border-[#ddd] rounded-xl hover:border-[#ee4d2d] hover:text-[#ee4d2d] transition-all font-bold no-underline text-[#3b2f2f]"
                  >
                    <ShoppingBag className="w-4 h-4" /> ShopeeFood
                  </a>
                </div>
              </section>

              {/* Resep & Bahan */}
              <section>
                <h2 className="font-serif text-xl text-[#3b2f2f] mb-4">Cara Penyajian Ataupun Pembuatan</h2>
                <div className="space-y-3">
                  <AccordionItem title="Daftar Bahan Utama">
                    <ul className="list-disc list-inside space-y-2 text-[#6e5849]">
                      {item.resep.bahan.map((b: string, i: number) => <li key={i}>{b}</li>)}
                    </ul>
                  </AccordionItem>
                  <AccordionItem title="Langkah-langkah Memasak">
                    <ol className="list-decimal list-inside space-y-2 text-[#6e5849]">
                      {item.resep.langkah.map((l: string, i: number) => <li key={i}>{l}</li>)}
                    </ol>
                  </AccordionItem>
                </div>
              </section>
            </div>
          </div>

          <div className="mt-20">
            <ReviewsSection />
          </div>
        </article>
      </main>
      <Footer />
    </>
  )
}